.author{
    text-align: right;
    margin-right: 1%;
    &__creator{
        font-size: 1em;
    }
    &__mail{
        color: white;
        text-decoration: none;
        font-size: 1em;
    }
}

@media (min-width: 1281px) {
    //DESKTOP
    .author{
        margin-right: 1%;
        &__creator{
            font-size: 1em;
        }
    }
  }

@media (min-width: 1025px) and (max-width: 1280px) {
   //LAPTOP
   .author{
    margin-right: 1%;
    &__creator{
        font-size: .9em;
    }
    &__mail{
        font-size: .9em;
    }
}
}
  

@media (min-width: 768px) and (max-width: 1024px){
    //TABLET PORTRAIT
    .author{
        margin-right: 1%;
        &__creator{
            font-size: 1em;
            margin-top: 3%;
        }
        &__mail{
            font-size: 1em;
        }
    }
  }
  

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape){
    //TABLET LANDSCAPE
    .author{
        margin-right: 1%;
        &__creator{
            font-size: .8em;
            margin-top: .5%;
        }
        &__mail{
            font-size: .8em;
        }
    }
  }
  

@media (min-width: 812px) and (max-width: 926px) and (orientation: landscape){
    //X-SERIES iPHONE LANDSCAPE
    .author{
        margin-right: 1%;
        &__creator{
            margin-top: 1%;
            font-size: .6em;
        }
        &__mail{
            font-size: .6em;
        }
    }
  }


@media (min-width: 481px) and (max-width: 767px){
    //PHONE LANDSCAPE
    .author{
        margin-right: 1%;
        &__creator{
            margin-top: 2%;
            font-size: .6em;
        }
        &__mail{
            font-size: .6em;
        }
    }
  }


@media (min-width: 320px) and (max-width: 480px){
    //PHONE PORTRAIT
    .author{
        text-align: center;
        margin-right: 1%;
        &__creator{
            margin-top: 10%;
            font-size: 1em;
        }
        &__mail{
            font-size: 1em;
        }
    }
  }
